async function bindleftCommonSearchdropdown(instance) {
    var vm = instance;
    var params = `countryId=223&includeCandaState=true`;
    await DataService.GetStateList(params).then((response) => {
        if (response.data != '') {
            if (response.data.length) {
                response.data.forEach(element => {
                    let obj = {
                        name: element.STATE_NAME,
                        value: element.STATE_ID
                    }
                    leftsearchSchema[3].listOptions.push(obj);
                });

            }
        }
    });

}
export default { buttons, leftsearchSchema, listActions, bindleftCommonSearchdropdown };