<template>
  <div class="change-history">

    <!-- Billing Configuration Changes -->
    <div class="config-section">
      <div 
        class="card-header" 
        @click="toggleSection('config')"
        :aria-expanded="(activeSection === 'config').toString()"
        tabindex="0"
        @keydown.enter="toggleSection('config')"
        @keydown.space.prevent="toggleSection('config')"
        style="cursor: pointer; display: flex; align-items: center;"
        role="button"
      >
        <i class="fa fa-angle-down rotate-icon" :class="{ rotated: activeSection === 'config' }" aria-hidden="true"></i>
        <h5 style="margin-left: 8px;">Billing Configuration Changes</h5>
      </div>

      <div v-show="activeSection === 'config'">
        <div class="version-comparison">
          <!-- Current Version -->
          <div class="version-column" v-if="currentChangeRuleData.length">
            <h6>Version Number: {{ currentVersion }}</h6>
            <div 
              class="history-item" 
              v-for="item in currentChangeRuleData" 
              :key="item.ConfigurationId"
            >
              <p><strong>Billing Rule Name:</strong> {{ item.BillingConfigName }}</p>
              <p><strong>Contract Name:</strong> {{ item.BillingRuleClientName }}</p>
              <p><strong>Created By:</strong> {{ item.BillingRuleCreatedBy }}</p>
              <p><strong>Created Date:</strong> {{ formatDate(item.BillingRuleCreatedAt) }}</p>
              <p><strong>Modified By:</strong> {{ item.BillingRuleModifyBy }}</p>
              <p><strong>Modified Date:</strong> {{ formatDate(item.BillingRuleModifyAt) }}</p>
              <p><strong>Effective From:</strong> {{ formatDate(item.EfficativeDateFrom) }}</p>
              <p><strong>Effective To:</strong> {{ formatDate(item.EfficativeDateTo) }}</p>
              <p><strong>Charge Type:</strong> {{ item.ChargeType || '--' }}</p>
              <p><strong>Applicable To All Utilities:</strong> {{ item.IsRuleApplicableToAll ? 'Yes' : 'No' }}</p>
              <p><strong>Utility Name:</strong> {{ item.UtilityName }}</p>
              <p><strong>Group By Columns:</strong> {{ item.BillingRuleGroupByName || '--' }}</p>
            </div>
          </div>

          <!-- Previous Version -->
          <div class="version-column" v-if="previousChangeRuleData.length">
            <h6>Version Number: {{ previousVersion }}</h6>
            <div 
              class="history-item" 
              v-for="item in previousChangeRuleData" 
              :key="item.ConfigurationId"
            >
              <p><strong>Billing Rule Name:</strong> {{ item.BillingConfigName }}</p>
              <p><strong>Contract Name:</strong> {{ item.BillingRuleClientName }}</p>
              <p><strong>Created By:</strong> {{ item.BillingRuleCreatedBy }}</p>
              <p><strong>Created Date:</strong> {{ formatDate(item.BillingRuleCreatedAt) }}</p>
              <p><strong>Modified By:</strong> {{ item.BillingRuleModifyBy }}</p>
              <p><strong>Modified Date:</strong> {{ formatDate(item.BillingRuleModifyAt) }}</p>
              <p><strong>Effective From:</strong> {{ formatDate(item.EfficativeDateFrom) }}</p>
              <p><strong>Effective To:</strong> {{ formatDate(item.EfficativeDateTo) }}</p>
              <p><strong>Charge Type:</strong> {{ item.ChargeType || '--' }}</p>
              <p><strong>Applicable To All Utilities:</strong> {{ item.IsRuleApplicableToAll ? 'Yes' : 'No' }}</p>
              <p><strong>Utility Name:</strong> {{ item.UtilityName }}</p>
              <p><strong>Group By Columns:</strong> {{ item.BillingRuleGroupByName || '--' }}</p>
            </div>
          </div>
        </div>
      </div>
    </div>

    <!-- Node Comparison Section -->
    <div class="node-section">
      <div 
        class="card-header" 
        @click="toggleSection('node')"
        :aria-expanded="(activeSection === 'node').toString()"
        tabindex="0"
        @keydown.enter="toggleSection('node')"
        @keydown.space.prevent="toggleSection('node')"
        style="cursor: pointer; display: flex; align-items: center; margin-top: 16px;"
        role="button"
      >
        <i class="fa fa-angle-down rotate-icon" :class="{ rotated: activeSection === 'node' }" aria-hidden="true"></i>
        <h5 style="margin-left: 8px;">Billing Rule Changes</h5>
      </div>

      <div class="node-comparison" v-show="activeSection === 'node'">
        <!-- Current Node -->
        <div class="node-version-column" v-if="nodeHistoryFullData && nodeHistoryFullData.length">
          <h6>Version Number: {{ currentVersion }}</h6>
          <div class="node-history-item" v-for="(node, index) in nodeHistoryFullData" :key="'current-' + index">
            <p><span class="node-label">Rule</span> <span class="node-value">{{ node.DISPLAY_NODE_NAME || node.SERVICE_NAME }}</span></p>
            <p><span class="node-label"></span> <span class="node-value">{{ node.CONDITION || '--' }}</span></p>
                 <i class="fa fa-eye text-primary fa-size" style="margin-left: 450px; position: relative; top: -25px;" @click="showRuleServiceUpdateForm(CONFIGURATION_ID,BILLING_SERVICE_ID,node.SERVICE_ID,node.DISPLAY_NODE_NAME)"></i>
          </div>
        </div>

        <!-- Previous Node -->
        <div class="node-version-column" v-if="nodeHistoryData && nodeHistoryData.length">
          <h6>Version Number: {{ previousVersion }}</h6>
          <div class="node-history-item" v-for="(node, index) in nodeHistoryData" :key="'previous-' + index">
            <p><span class="node-label">Rule</span> <span class="node-value">{{ node.DISPLAY_NODE_NAME || node.SERVICE_NAME }}</span></p>
            <p><span class="node-label"></span> <span class="node-value">{{ node.CONDITION || '--' }}</span></p>
               <i class="fa fa-eye text-primary fa-size" style="margin-left: 450px; position: relative; top: -25px;" @click="showRuleServiceUpdateForm(CONFIGURATION_ID,BILLING_SERVICE_ID,node.SERVICE_ID,node.DISPLAY_NODE_NAME)"></i>

          </div>
        </div>
      </div>
    </div>

    <!-- Flow Chart Section -->
    <div 
      class="card-header" 
      @click="toggleSection('chart')"
      :aria-expanded="(activeSection === 'chart').toString()"
      tabindex="0"
      @keydown.enter="toggleSection('chart')"
      @keydown.space.prevent="toggleSection('chart')"
      style="cursor: pointer; display: flex; align-items: center; margin-top: 16px;"
      role="button"
    >
      <i class="fa fa-angle-down rotate-icon" :class="{ rotated: activeSection === 'chart' }" aria-hidden="true"></i>
      <h5 style="margin-left: 8px;">Flow Chart</h5>
    </div>

    <div v-show="activeSection === 'chart'" class="flowchart-scroll-wrapper">
      <div class="flowchart-container">
        <div class="chart-column">
          <h6 style="text-align: center;">Version Number: {{ currentVersion }}</h6>
          <BillingChartHistory :CONFIGURATION_ID="CONFIGURATION_ID" />
        </div>

        <div class="chart-column">
          <h6 style="text-align: center;">Version Number: {{ previousVersion }}</h6>
          <BillingChartHistory :CONFIGURATION_ID="OLD_RULE_ID" :showHistory="1" />
        </div>
      </div>
    </div>
<div>
     <RuleServiceUpdate 
      v-if="isRuleServiceUpdateFormVisible" 
      :configurationId="selectedConfigurationId"
      :billingServiceId="BILLING_SERVICE_ID"
      :conditionId ="conditionId"
      :nodeName="nodeName"
      reqFrom="ViewBillingConditionForService"
      closeModalType="isRuleServiceUpdateFormVisible"
    />
  </div>

  </div>
</template>

<script>
import BillingChartHistory from '../../../views/BillingRule/BillingRuleHistory/BillingChartHistory.vue';
 import RuleServiceUpdate from '../../../views/BillingRule/BillingRuleHistory/RuleServiceUpdate.vue';


export default {
  name: 'FlowChartWrapper',
  components: { BillingChartHistory,RuleServiceUpdate },
  props: {
    currentChangeRuleData: Array,
    previousChangeRuleData: Array,
    currentVersion: Number,
    previousVersion: Number,
    NEW_RULE_ID: Number,
    OLD_RULE_ID: Number,
    currentConfigurationId: Number,
    addRootNode: Boolean,
    includeGlobalService: Boolean,
    includeDeletedService: Boolean,
    CONFIGURATION_ID: Number,
    nodeHistoryData: Array,
    nodeHistoryFullData: Array,
    serviceId:Array,
    BILLING_SERVICE_ID:Number
  },

  data() {
    return {
      activeSection: 'config',
      isRuleServiceUpdateFormVisible: false,
      conditionId:0,
      nodeName:''
    };
  },

  methods: {
    formatDate(date) {
      if (!date) return '--';
      return new Date(date).toLocaleDateString();
    },

    toggleSection(sectionName) {
      this.activeSection = this.activeSection === sectionName ? '' : sectionName;
    },
     showRuleServiceUpdateForm(CONFIGURATION_ID,BILLING_SERVICE_ID,SERVICE_ID,NODE_NAME) {
      debugger
      this.selectedConfigurationId = CONFIGURATION_ID; 
      this.conditionId  = SERVICE_ID
      this.BILLING_SERVICE_ID=BILLING_SERVICE_ID;
      this.nodeName =NODE_NAME;
      this.isRuleServiceUpdateFormVisible = true; 
    }
  }
};
</script>


<style scoped>
.change-history {
  padding: 16px;
}

.version-comparison {
  display: flex;
  gap: 16px;
  align-items: flex-start;
  overflow-x: auto;
  padding-bottom: 8px;
}

.version-column {
  flex: 1 1 300px;
  background: #f7f7f7;
  padding: 12px;
  border-radius: 4px;
  min-width: 300px;
  box-sizing: border-box;
}

.version-column h6 {
  margin-bottom: 12px;
  border-bottom: 1px solid #ccc;
  padding-bottom: 4px;
}

.history-item p {
  background: #e5e5e5;
  padding: 6px 10px;
  margin: 4px 0;
  border-radius: 3px;
  word-wrap: break-word;
}

/* Node Section */
.node-section {
  margin-top: 16px;
}

.node-comparison {
  display: flex;
  gap: 16px;
  align-items: flex-start;
  overflow-x: auto;
  padding-top: 16px;
}

.node-version-column {
  flex: 1 1 300px;
  background: #f9f9f9;
  padding: 12px;
  border-radius: 6px;
  min-width: 300px;
  box-shadow: 0 1px 4px rgba(0, 0, 0, 0.1);
}

.node-version-column h6 {
  margin-bottom: 12px;
  border-bottom: 1px solid #ccc;
  padding-bottom: 6px;
  font-weight: 600;
}

.node-history-item {
  background: #e9ecef;
  padding: 8px 12px;
  margin-bottom: 8px;
  border-radius: 4px;
}

.node-history-item p {
  margin: 4px 0;
  font-size: 16px;
  line-height: 1.4;
  word-wrap: break-word;
}

.node-label {
  font-weight: 600;
  color: #333;
  margin-right: 4px;
}

.node-value {
  color: #555;
}

/* Rotate icon for expand/collapse */
.rotate-icon {
  transition: transform 0.3s ease;
}

.rotate-icon.rotated {
  transform: rotate(180deg);
}

/* Card header styling */
.card-header {
  user-select: none;
}
.flowchart-container {
  display: flex;
  gap: 20px;
  flex-wrap: nowrap;
  overflow-x: auto;
  margin-top: 10px;
}

.chart-column {
  flex: 1 1 50%;
  background: #f9f9f9;
  padding: 10px;
  border-radius: 6px;
  min-width: 600px;
  box-shadow: 0 1px 2px rgba(0, 0, 0, 0.1);
}

</style>
